* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.6;
}

body {
  margin: 0;
}

img {
  width: 100%;
}

h1,h2,h3,h4,h5,h6 {
  margin-top: 0;
  font-weight: normal;
}

h1 {
  font-size: rem(60px);
  margin-bottom: rem(20px);
}

p {
  margin: 0 0 rem(12px);
  max-width: 60ch;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a{
  color: inherit;
  text-decoration: none;
}